* {
  font-family: Roboto, sans-serif; }

.sb-show-main {
  padding: 20px;
  position: relative; }

.TextField {
  max-width: 300px; }

.row {
  display: flex;
  flex-flow: row nowrap;
  align-items: center; }
  .row.right {
    justify-content: flex-end; }

.column {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start; }

.line {
  min-height: 18px; }

a {
  text-decoration: none;
  color: lightblue; }

.sb-show-main .FormUploadProgress {
  transition-delay: 0s !important;
  bottom: unset;
  right: unset; }

.AutocompleteSelect.menu {
  min-width: 0; }
  .AutocompleteSelect.menu .popup {
    left: 0; }
    .AutocompleteSelect.menu .popup .DummyChild {
      font-size: 14px;
      color: #818181; }
    .AutocompleteSelect.menu .popup .material-icons {
      color: #818181;
      margin-right: 4px; }
  .AutocompleteSelect.menu.active .popup {
    top: 33px; }
  .AutocompleteSelect.menu > .selected {
    border: none; }
    .AutocompleteSelect.menu > .selected.nolabel {
      border: none; }
    .AutocompleteSelect.menu > .selected .material-icons {
      color: #A6A6A6;
      transition: color 0.2s ease; }
    .AutocompleteSelect.menu > .selected .ItemWrapper.selected {
      padding-left: 15px;
      margin-left: -5px; }
  .AutocompleteSelect.menu.active .selected .material-icons {
    color: #333333; }
