.TextField {
  position: relative;
  width: 100%; }
  .TextField > label {
    font-family: Roboto, sans-serif;
    color: rgba(0, 0, 0, 0.3);
    font-size: 16px;
    position: absolute;
    left: 0px;
    top: 16px;
    margin-top: -16px;
    transition: all 0.2s ease;
    user-select: none;
    cursor: text; }
  .TextField > input {
    font-family: Roboto, sans-serif;
    color: #1e252d;
    height: 19px;
    font-size: 16px;
    width: 100%;
    border: 0px;
    padding-bottom: 4px;
    padding-top: 0;
    outline-color: rgba(0, 0, 0, 0); }
    .TextField > input:focus {
      outline: none; }
    .TextField > input:disabled {
      color: gray;
      background-color: white; }
    .TextField > input:disabled + .border {
      height: 0px;
      border-bottom: 2px dotted #e0e0e0; }
    .TextField > input::placeholder {
      font-family: Roboto, sans-serif;
      color: rgba(0, 0, 0, 0.3);
      opacity: 1; }
  .TextField > .border {
    border-bottom: 1px solid #e0e0e0;
    height: 1px;
    position: relative; }
    .TextField > .border .line {
      left: 50%;
      width: 0;
      height: 2px;
      min-height: 2px;
      background-color: #00bcd4;
      transition: all 0.2s ease;
      position: absolute; }
  .TextField > .error {
    text-align: left;
    font-size: 10px;
    color: #f44336;
    margin-top: 2px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.2s ease; }
  .TextField.focused > label, .TextField.withValue > label {
    top: 4px;
    font-size: 12px;
    line-height: 14px; }
  .TextField.errored > .error {
    visibility: visible;
    opacity: 1; }
  .TextField.errored > .border {
    border-bottom-color: #f44336; }
  .TextField.errored > label {
    color: #f44336; }
  .TextField.focused > label {
    color: #00bcd4; }
  .TextField.focused > .border .line {
    left: 0;
    width: 100%; }
  .TextField.disabled label, .TextField.disabled input {
    cursor: not-allowed; }
  .TextField.withLabel {
    margin-top: 15px; }
  .TextField input::-webkit-contacts-auto-fill-button {
    visibility: hidden;
    display: none !important;
    pointer-events: none;
    position: absolute;
    right: 0; }
  .TextField input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0px 9999px white; }

@keyframes shake {
  0%, 100% {
    transform: translateX(0); }
  20%, 60% {
    transform: translateX(-5px); }
  40%, 80% {
    transform: translateX(5px); } }
  .TextField .shake {
    animation-duration: .5s;
    animation-fill-mode: both;
    animation-timing-function: ease-in-out;
    animation-name: shake; }
